<template>
    <div id="myModal" class="modal">
        <div class="modal-content">
            <div class="modal-header">
            <span class="modal-close" v-on:click="popupClose">×</span>
            </div>
            
            <div class="modal-body">
                <div class="container">
                    <h2>Game Over</h2>
                    <p>Score: <span class="score">{{this.score}}</span></p>
                </div>
            </div>
            
        </div>
    </div>
</template>

<script>
export default {
    name: 'GameOver',
    props: ['score', ],
    methods: {
        popupClose(){
            document.getElementById('myModal').style.display = "none";
        }
    }
}

</script>

<style scoped>
/* The Modal (background) */
#myModal.modal {
    position: fixed; /* Stay in place */
    z-index: 99999999; /* Sit on top */
    padding-top: 50px; /* Location of the box */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

/* Modal Content */
#myModal .modal-content {
    position: relative;
    background-color: #fefefe;
    margin: auto;
    padding: 0;
    border: 1px solid #888;
    width: 80%;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);
    -webkit-animation-name: animatetop;
    -webkit-animation-duration: 0.4s;
    animation-name: animatetop;
    animation-duration: 0.4s;
    max-width: 600px;
    top: 50%;
    transform: translateY(-50%);
}

/* Add Animation */
@-webkit-keyframes animatetop {
    from {top:-300px; opacity:0} 
    to {top:50%; opacity:1}
}

@keyframes animatetop {
    from {top:-300px; opacity:0}
    to {top:50%; opacity:1}
}

/* The Close Button */
#myModal  .modal-close {
    color: white;
    float: right;
    font-size: 28px;
    font-weight: bold;
}

#myModal .modal-close:hover,
#myModal  .modal-close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
}

#myModal  .modal-header {
    padding: 2px 16px;
    color: white;
}

#myModal  .modal-body {padding: 20px 16px; color: #000; text-align: center;}
#myModal  .modal-body  .container{height: 100%;}

</style>